body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  background-color: #e4e8e68c;
}

.image-cover {
  background-image: url(/recipe-finder/static/media/hero-bg.c5b45d5f.jpg);
}

.form-outline {
  width: 400px;
}

.form-outline .form-control:focus ~ .form-notch .form-notch-leading {
  border-top: 0.125rem solid #000;
  border-bottom: 0.125rem solid #000;
  border-left: 0.125rem solid #000;
  box-shadow: none;
}

.form-outline .form-control:focus ~ .form-notch .form-notch-middle {
  border-bottom: 0.125rem solid #000;
  box-shadow: none;
}

.form-outline .form-control:focus ~ .form-notch .form-notch-trailing {
  border-bottom: 0.125rem solid #000;
  border-right: 0.125rem solid #000;
  border-top: 0.125rem solid #000;
  box-shadow: none;
}

.form-outline .form-control:focus ~ .form-label {
  color: #000;
  box-shadow: none;
}

.card-image {
  width: 160px;
}

.category-container {
  margin-top: 1rem;
  padding-bottom: 3rem;
}

.card-container {
  max-width: 160px;
  text-overflow: ellipsis;
}

.title-category {
  font-size: 2vh;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@media screen and (max-width: 600px) {
  .card-image {
    width: 110px;
  }
  .category-container {
    margin: 0 5px 0 5px;
  }
  .card-container {
    max-width: 110px;
  }
}

.recipe-cards {
  margin: 20px 8rem;
}

.card-body {
  padding: 1rem 1rem;
}

